<template>
  <div class="ask">
    <Header></Header>
    <Banner></Banner>
    <div class="ask-con">
      <div class="sak-c">
        <div class="ask-fir">
          <span style="color:#fc4b16;"></span> <span style="color:#666666;">></span> <span></span>
        </div>
        <div class="ask-item" @click="goDetail(item.id)" v-for="item in dataList">
          <div class="ask-item-title" v-text="item.title"></div>
          <div class="ask-item-con" v-text="item.con"></div>
          <div class="ask-item-time" v-text="item.time"></div>
        </div>
        <div class="ask-btn">
          <div class="ask-btn-shouye"></div>
          <div class="ask-btn-shangye">&lt;</div>
          <div class="numbers">1</div>
          <div class="ask-btn-shouye" style="margin-left:18px;">&gt;</div>
          <div class="ask-btn-shangye"></div>
        </div>
        <div style="height: 80px;"></div>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
  import Header from '../../components/Header'
  import Banner from '../../components/Banner'
  import Footer from '../../components/Footer'
  export default {
    name: 'Answer',
    components: { Header, Banner, Footer },
    data () {
      return {
        dataList: [{
          title: '',
          con: '...',
          time: '2019613  16:55:21',
          id: 'ask1'
        },{
          title: '?',
          con: ',...',
          time: '2019613  16:55:21',
          id: 'ask2'
        },{
          title: '',
          con: '...',
          time: '2019613  16:55:21',
          id: 'ask3'
        },{
          title: '?',
          con: '...',
          time: '2019613  16:55:21',
          id: 'ask4'
        },{
          title: '|',
          con: '...',
          time: '2019613  16:55:21',
          id: 'ask5'
        },{
          title: '',
          con: '2830...',
          time: '2019613  16:55:21',
          id: 'ask6'
        },{
          title: '',
          con: '...',
          time: '2019613  16:55:21',
          id: 'ask7'
        }]
      }
    },
    methods: {
      goDetail (id) {
        this.$router.push({
          name: 'answer',
          query: {
            id: id
          }
        })
      }
    }
  }
</script>

<style scope>
  .ask-con {
    display: flex;
    flex-direction: row;
    justify-content: center;
    background: #f3f3f3;
  }
  .sak-c {
    width: 1204px;
  }
  .ask-fir {
    height: 148px;
    width: 100%;
    line-height: 170px;
    font-size: 20px;
    color: #333333;
  }
  .ask-item {
    height: 242px;
    width: 100%;
    border-bottom: 1px dashed #dcdcdc;
    background: #ffffff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 38px;
    font-size: 18px;
    color: #666666;
  }
  .ask-item:hover {
    cursor: pointer;
  }
  .ask-item-title {
    font-size: 26px;
    color: #333333;
    margin-bottom: 16px;
  }
  .ask-item-time {
    color: #999999;
    margin-top: 16px;
  }
  .ask-btn {
    height: 130px;
    background: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .ask-btn-shouye {
    width: 85px;
    height: 30px;
    border: solid 1px #dcdcdc;
    text-align: center;
    line-height: 30px;
    font-size: 18px;
    color: #333333;
  }
  .ask-btn-shangye {
    width: 85px;
    height: 30px;
    border: solid 1px #dcdcdc;
    border-left: none;
    text-align: center;
    line-height: 30px;
    font-size: 18px;
    color: #333333;
    margin-right: 18px;
  }
  .numbers {
    width: 30px;
    height: 30px;
    border: solid 1px #dcdcdc;
    text-align: center;
    line-height: 30px;
    font-size: 18px;
    color: #333333;
  }
  .ask-btn div:hover {
    cursor: pointer;
  }

</style>



