import Vue from 'vue'
import Router from 'vue-router'
import Home from '../views/home/home'
import OutLine from '../views/outLine/outLine'
import Evaluate from '../views/evaluate/evaluate'
import Story from '../views/story/story'
import Inside from '../views/inside/inside'
import Answer from '../views/answer/answer'
import Ask from '../views/ask/ask'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/outLine',
      name: 'outLine',
      component: OutLine
    },
    {
      path: '/evaluate',
      name: 'evaluate',
      component: Evaluate
    },
    {
      path: '/story',
      name: 'story',
      component: Story
    },
    {
      path: '/inside',
      name: 'inside',
      component: Inside
    },
    {
      path: '/answer',
      name: 'answer',
      component: Answer
    },
    {
      path: '/ask',
      name: 'ask',
      component: Ask
    }
  ]
})



