<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
import './assets/reset.css'
import './assets/common.css'

export default {
  name: 'App',
  watch:{
    '$route':function(to,from){
      document.body.scrollTop = 0;
     document.documentElement.scrollTop = 0;
    }
  }

}
</script>

<style>
  #app {
    width: 100%;
    min-width: 1204px;
    height: 100%;
  }
</style>



