<template>
  <div class="banner container">
    <div class="content bannerContent">
      <div class="txt">
        <span v-for="(item, index) in bannerTxt" v-text="item" :key="index"></span>
      </div>
      <div class="defaultPic" :class="{storyPic: routePath === 'story'}"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Banner',
  data () {
    return {
      bannerTxt: [],
      routePath: ''
    }
  },
  created () {
      this.routePath = this.$route.name
    switch (this.$route.name) {
      case 'home':
        this.bannerTxt = ['', '']
        break
      case 'outLine':
      case 'ask':
      case 'answer' :
        this.bannerTxt = ['', '']
        break
      case 'evaluate':
        this.bannerTxt = ['', '', '100%', '', '']
        break
      case 'story':
        this.bannerTxt = ['', '10w+']
        break
      case 'inside':
        this.bannerTxt = ['', '']
        break
    }
  }
}
</script>

<style scope>
  .banner {
    height: 502px;
    background: url(../assets/imgs/defaultBanner.png) no-repeat 100% 100%;
    border-bottom: 1px dotted #16d0ff;
  }
  .bannerContent {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .txt {
    margin-left: 50px;
    display: flex;
    flex-direction: column;
    font-size: 24px;
  }
  .txt span:first-child {
    font-size: 44px;
    font-weight: bold;
  }
  .txt:first-child {

  }
  .defaultPic {
    margin-right: 99px;
    width: 348px;
    height: 347px;
    background: url(../assets/imgs/bannerPic.png) no-repeat;
  }
  .storyPic {
    margin-top: 124px;
    margin-right: 24px;
    width: 421px;
    height: 376px;
    background: url(../assets/imgs/story1.png) no-repeat;
  }
</style>



