<template>
  <div class="header container">
    <div class="content headerContent">
      <img src="../assets/imgs/logo.png" alt="">
      <div class="loop">
        <span @click="goPath('/')" :class="{active: routerPath === 'home'}"></span>
        <span class="baoxian" @click="goPath('outLine')" :class="{active: (routerPath === 'outLine' || routerPath === 'ask' || routerPath === 'answer')}"></span>
        <div class="child">
          <div></div>
          <div></div>
          <div></div>
          <div :class="{active: (routerPath === 'ask' || routerPath === 'answer')}" @click="goPath('ask')"></div>
        </div>
        <span @click="goPath('evaluate')" :class="{active: routerPath === 'evaluate'}"></span>
        <span @click="goPath('story')" :class="{active: routerPath === 'story'}"></span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
      routerPath: ''
    }
  },
  created () {
    this.routerPath = this.$route.name
  },
  methods: {
    goPath (path) {
      this.$router.push(path)
    }
  }
}
</script>

<style scope>
  .child {
    position: absolute;
    width: 135px;
    height: 187px;
    background: #ffffff;
    top: 66px;
    left:150px;
    flex-direction: column;
    font-size: 20px;
    color: #666666;
    justify-content: space-around;
    align-items: center;
    display: none;
  }
  .child:hover {
    display: flex;
  }
  .child div:hover {
    color: #fc4b16;
    cursor: pointer;
  }
  .child div {
    line-height: 0;
  }
  .baoxian:hover + .child {
    display: flex;
  }
  .header {
    height: 66px;
  }
  .headerContent {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-size: 20px;
    color: #666666;
  }
  .loop {
    position: relative;
    height: 66px;
  }
  .loop span {
    display: inline-block;
    margin-left: 65px;
    height: 100%;
    line-height: 66px;
  }
  .loop span:hover {
    cursor: pointer;
    color: #fc4b16;
  }
  .active {
    color: #fc4b16;
  }
</style>



