<script>
  import Header from '../../components/Header'
  import Banner from '../../components/Banner'
  import Footer from '../../components/Footer'
  export default {
    name: 'Answer',
    components: { Header, Banner, Footer },
    data () {
      return {
        id: '',
        dataRoom: {
          'ask1': {
            title1: '',
            title2: '',
            time: '2019-08-29 19:25:22',
            time1: '2019-08-29 19:25:22',
            time2: '2019-08-29 19:25:22',
            time3: '2019-08-29 19:25:22',
            anser1: '5560100%',
            anser2: '160234+80/',
            anser3: '1015%10%100%,'
