  .answer-r1 {
    width: 354px;
    height: 440px;
    position: absolute;
    right: 0;
    top: 148px;
    background: #ffffff;
    padding: 0 26px;
    display: flex;
    flex-direction: column;
  }
  .r1-1 {
    height: 50px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-top: 36px;
  }
  .r1-item {
    margin-top: 20px;
    width: 230px;
    height: 40px;
    border-radius: 20px;
    background-color: #f3f3f3;
    font-size: 18px;
    color: #333333;
    text-align: center;
    line-height: 40px;
  }
</style>



